* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.titlebar{
  -webkit-app-region: drag;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 30px;
  z-index: 10000;
}
body {
  min-height: 100vh;
  background: #050505;
  color: #f7f7f7;
  font-family: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    sans-serif;
  -webkit-font-smoothing: antialiased;
}

.app-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 56px 16px;
  position: relative; /* Enable absolute positioning for children */
}

.background-layer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1; 
  overflow: hidden;
  pointer-events: auto;
  opacity: 0.1;
}

.welcome-shell {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 18px;
  text-align: center;
  max-width: 600px;
  position: relative; /* Ensure it's above the background */
  z-index: 1; /* Above background layer */
}

.welcome-headline {
  font-size: clamp(44px, 9vw, 80px);
  font-weight: 700;
  letter-spacing: -0.015em;
}

.split-parent {
  position: relative;
}

.split-char,
.split-word {
  display: inline-block;
  white-space: pre;
  will-change: transform, opacity;
}

.split-line {
  display: block;
  width: 100%;
}

.welcome-tagline {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.68);
}

.welcome-note {
  font-size: 15px;
  color: rgba(255, 255, 255, 0.48);
}

@media (max-width: 640px) {
  .app-container {
    padding: 48px 20px;
  }

  .welcome-shell {
    gap: 16px;
  }

  .welcome-tagline {
    font-size: 16px;
  }

  .welcome-note {
    font-size: 14px;
  }
}

#start-button {
  margin-top: 24px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  width: 200px;
  color: #fff;
  background-color: #B0B0B0;
  transition: background-color 0.3s ease;
}

/* Video and Chat Layout */
.video-chat-wrapper {
  display: flex;
  gap: 20px;
  margin-top: 20px;
  align-items: flex-start;
}

.video-section {
  flex: 1;
  min-width: 0; /* Allows flex item to shrink below content size */
}

/* Chat Container Styles */
.ChatContainer {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 16px;
  background: #1a1a1a;
  border-radius: 8px;
  width: 400px;
  flex-shrink: 0;
}

/* Responsive: Stack vertically on smaller screens */
@media (max-width: 1200px) {
  .video-chat-wrapper {
    flex-direction: column;
  }
  
  .ChatContainer {
    width: 100%;
  }
}

.chat-messages-area {
  height: 300px;
  overflow-y: auto;
  border: 1px solid #333;
  border-radius: 4px;
  padding: 12px;
  background: #0a0a0a;
}

.chat-messages-area::-webkit-scrollbar {
  width: 8px;
}

.chat-messages-area::-webkit-scrollbar-track {
  background: #0a0a0a;
}

.chat-messages-area::-webkit-scrollbar-thumb {
  background: #444;
  border-radius: 4px;
}

.chat-messages-area::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.chat-message {
  margin-bottom: 12px;
  padding: 10px 14px;
  border-radius: 8px;
  max-width: 80%;
  word-wrap: break-word;
}

.chat-message.user {
  background-color: #e3f2fd;
  color: #000;
  margin-left: auto;
  text-align: right;
}

.chat-message.assistant {
  background-color: #f1f8e9;
  color: #000;
}

.chat-message.error {
  background-color: #ffebee;
  color: #c62828;
}

.chat-message-role {
  font-weight: bold;
  margin-bottom: 4px;
  font-size: 13px;
}

.chat-message-text {
  font-size: 14px;
  line-height: 1.5;
}

.chat-empty-state {
  color: #666;
  text-align: center;
  padding: 40px 20px;
  font-style: italic;
}

.chat-input-area {
  display: flex;
  gap: 8px;
  align-items: center;
}

.chat-input {
  flex: 1;
  padding: 10px 14px;
  border: 1px solid #333;
  border-radius: 4px;
  background: #0a0a0a;
  color: #fff;
  font-size: 14px;
  font-family: inherit;
}

.chat-input:focus {
  outline: none;
  border-color: #555;
}

.chat-input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.chat-send-button {
  padding: 10px 20px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: background 0.2s ease;
}

.chat-send-button:hover:not(:disabled) {
  background: #0056b3;
}

.chat-send-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.chat-clear-button {
  padding: 10px 20px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: background 0.2s ease;
}

.chat-clear-button:hover {
  background: #c82333;
}

/* Quiz Styles */
.quiz-button {
  padding: 10px 20px;
  background: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: background 0.2s ease;
}

.quiz-button:hover {
  background: #218838;
}

.quiz-button-personalized {
  padding: 10px 20px;
  background: #ffc107;
  color: #000;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: background 0.2s ease;
}

.quiz-button-personalized:hover {
  background: #e0a800;
}

.quiz-loading {
  text-align: center;
  padding: 40px 20px;
  color: #fff;
  font-size: 16px;
}

.quiz-complete {
  text-align: center;
  padding: 40px 20px;
  color: #fff;
}

.quiz-complete h2 {
  margin-bottom: 20px;
  font-size: 32px;
}

.quiz-complete p {
  font-size: 18px;
  margin: 10px 0;
}

.quiz-question-container {
  padding: 20px;
}

.quiz-progress {
  text-align: center;
  color: #999;
  font-size: 14px;
  margin-bottom: 20px;
}

.quiz-question {
  font-size: 20px;
  font-weight: 600;
  color: #fff;
  margin-bottom: 24px;
  line-height: 1.5;
}

.quiz-options {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 20px;
}

.quiz-option {
  padding: 16px 20px;
  background: #2a2a2a;
  color: #fff;
  border: 2px solid #444;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  text-align: left;
  transition: all 0.2s ease;
}

.quiz-option:hover:not(:disabled) {
  background: #333;
  border-color: #666;
}

.quiz-option.selected {
  background: #1a4d8f;
  border-color: #2563eb;
}

.quiz-option.correct {
  background: #166534;
  border-color: #22c55e;
}

.quiz-option.incorrect {
  background: #7f1d1d;
  border-color: #ef4444;
}

.quiz-option:disabled {
  cursor: not-allowed;
}

.quiz-explanation {
  background: #1e3a5f;
  border-left: 4px solid #3b82f6;
  padding: 16px;
  border-radius: 4px;
  margin-bottom: 20px;
  color: #fff;
}

.quiz-explanation strong {
  display: block;
  margin-bottom: 8px;
  font-size: 16px;
}

.quiz-explanation p {
  margin: 0;
  line-height: 1.6;
}

.quiz-actions {
  display: flex;
  justify-content: center;
  gap: 12px;
}

.Selected-lessons-container {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  padding: 20px 0;
  justify-content: flex-start;
  align-items: flex-start;
}

.Selected-lesson-item {
  position: relative;
  flex: 0 0 auto;
  width: calc(33.333% - 20px);
  min-width: 300px;
  max-width: 400px;
  transition: all 0.3s ease;
  border: 2px magenta;
  border-radius: 40px;
  box-shadow: 0 0 30px rgba(139, 7, 146, 0.7);
}

@media (max-width: 1400px) {
  .Selected-lesson-item {
    width: calc(50% - 20px);
  }
}

@media (max-width: 900px) {
  .Selected-lesson-item {
    width: 100%;
    max-width: 500px;
  }
}

.results-container {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  padding: 20px 0;
  justify-content: flex-start;
  align-items: flex-start;
}

.result-items {
  position: relative;
  flex: 0 0 auto;
  width: calc(33.333% - 20px);
  min-width: 300px;
  max-width: 400px;
  transition: all 0.3s ease;
  border: 2px magenta;
  border-radius: 40px;
  box-shadow: 0 0 20px rgba(139, 7, 146, 0.7);
}

@media (max-width: 1400px) {
  .result-items {
    width: calc(50% - 20px);
  }
}

@media (max-width: 900px) {
  .result-items {
    width: 100%;
    max-width: 500px;
  }
}

/* Animated Gradient Keyframes - RGB LED Strip Effect */
@keyframes rgbBorderRotate {
  0% {
    background-position: 0% 50%;
    opacity: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
    opacity: 50% 100%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes rgbGlow {
  0% {
    filter: hue-rotate(0deg) brightness(1.2);
  }
  100% {
    filter: hue-rotate(360deg) brightness(1.2);
  }
}

.search-input {
  width: 100%;
  margin-top: 30px;
  margin-bottom: 40px;
  padding: 12px 16px;
  font-size: 16px;
  border-radius: 20px;
  background: #0a0a0a;
  color: #fff;
  font-family: inherit;
  outline: none;
  /* Default low shadow */
  border: 1px solid #333;
  box-shadow: 
    0 0 5px rgba(100, 100, 100, 0.2);
  transition: all 0.3s ease;
  position: relative;
} 

.search-input:focus {
  /* RGB gradient border effect */
  border: 1px groove transparent;
  background: 
    linear-gradient(#0a0a0a, #0a0a0a) padding-box,
    linear-gradient(
      90deg,
      #ff0000 0%,
      #ff7f00 14%,
      #ffff00 28%,
      #00ff00 42%,
      #0000ff 57%,
      #4b0082 71%,
      #9400d3 85%,
      #ff0000 100%
    ) border-box;
  background-size: 400% 400%;
  animation: rgbBorderRotate 9s linear infinite, rgbGlow 6s linear infinite;
  
  /* RGB glow effect around the border */
  box-shadow: 
    0 0 40px rgba(255, 0, 0, 0.3),
    0 0 40px rgba(0, 255, 0, 0.2),
    0 0 40px rgba(0, 0, 255, 0.1),
    inset 0 0 10px rgba(255, 255, 255, 0.05);
}


.cimg {
  width: 500px;
  margin-top: 60px;

}

.rimg {
  width: 250px;
  margin-top: 60px;
} 


.custom-scroll-text {
  bottom: 0;
  position: relative;
}